.wrapper {
   max-width: 600px;
   max-height: 600px;
   margin: 30px auto;     
   border: 1px solid #070707;
   border-radius: 5px;
   background-color: #1a844d;
   }
   h1 {
   color: #1a844d;
   width: -20px;
   display: center;
   text-align: left;
   font-weight: thin;
   font-family: fantasy;
   letter-spacing: 10px;
   font-size: 30px;
   margin-left: 130px;
   }
   .title {
   background-color:  rgb(222, 253, 243);
   margin: auto;
   }
   h2 {
   color: #010c06; 
   text-align: center;
   font-size: 20px;
   margin-top: 10px;
   }
   h3 {
   color: rgb(250, 242, 242);
   text-align: center;
   font-weight: bold;
   margin-top: 10px;
   margin-bottom: 10px;
   }
   #description {
   text-align: left;
   font-weight: bold;
   }
   .col-1, .col-2, .col-3, .weather, .maindate { 
   display: inline-block;
   margin-right: 10px;   
   color: rgb(245, 242, 242);
   }
   li {
   list-style: none;
   color: rgb(245, 242, 242);
   font-size: 20px;
   text-align: right;
   padding-bottom: 10px;
   font-weight: bold;
   }
   .weather-forecast {
   text-align: center;
   color: rgb(245, 242, 242);
   font-size: 20px;
   margin-top: 20px;
   margin-right: 25px;
   font-weight: bold;
   margin-bottom: 30px;
   }
   .weather-forecast-date {
   font-size: 22px;
   color: rgb(245, 242, 242);
   text-align: center; 
   }
   .button, button {
   border: 1px solid #1a844d;
   background-color:   rgb(222, 253, 243);
   font-size: 15px;
   font-weight: bold; 
   width: 100px;
   height: 40px;
   transition: all 200 ms ease-in-out;
   border-radius: 10px;
   border-color: #0b0b0b;
   box-shadow:  1px 1px 0 #fff, inset 0 1px 1px rgba(0, 0, 0, 0.3);
   }
   .button:hover {
   color: rgb(15, 15, 16);
   background-color: white;
   }
   .string {
   width: 300px;
   height: 39px;  
   text-align: left;
   font-weight: bold;  
   font-size: 15px;
   font-family: 'Times New Roman', Times, serif;
   border-radius: 10px;;
   margin-top: 30px;
   margin-left: 45px;
   }
   .path {
   display: block;
   margin-top: 15px;
   display: center;
   font-size: 15px;
   text-align: left;
   letter-spacing: normal;
   }
   a {
      color: black;  
      text-align: left;
   }
   #temperature {
   display: center;
   margin-left: 30px;
   font-weight: bold;
   font-size: 20px;
   }
   #city {
   margin-top: 5px;
   margin-bottom: 10px;
   }
   .maincity {
   padding-right: 40px; 
   text-align: center;  
   }
   #date {
   margin-top: 0;
   margin-bottom: 0;
   padding-left: 20px; 
   font-weight: bold; 
   font-size: 25px;
   }
   .weather-forecast-temperature-max {
   font-weight: bold;
   margin-left: 20px;
   }
   .line {
   border-bottom: 3px solid white;
   margin: 0 40px 0 40px; 
   }
   #icon {
   margin-left: 10px;
   }
   p { 
   word-spacing: 40px;
   margin-left: 55px;
   color:rgb(250, 242, 242);
   font-size: 20px;
   margin-top: 0;
   font-weight: bold; 
   }
   